.nav-container {
  position: fixed;
  width: 100vw;
  height: 6em;
  display: grid;
  grid-template-columns: 150px 1fr 1fr 2fr 150px;
  grid-template-rows: 4em;
  top: 2em;
  background-color: #2d2f31;
  z-index: 2;

  .logo-container {
    position: relative;
    width: 6em;
    height: auto;
    grid-column: 2/3;

    #nav-logo {
      position: absolute;
      left: 0;
      top: 3px;
    }
  }

  .link-container {
    position: relative;
    // background-color: white;
    grid-column: 4/5;
    display: flex;
    align-items: center;
    justify-content: space-around;
    font-size: 2em;
    font-weight: 500;
    overflow: hidden;
    margin-top: 1em;

    :nth-child(n) {
      cursor: pointer;
    }

    .home-link {
      color: #429ffe;

      &:after {
        content: "";
        position: absolute;
        top: inherit;
        left: inherit;
        width: inherit;
        height: 2px;
        background-color: #429ffe;
      }
    }

    .about-me-link {
      color: #e96feb;

      //   &:after {
      //     content: "";
      //     position: absolute;
      //     top: inherit;
      //     left: inherit;
      //     box-shadow: -35px 14px 65px 25px #e96feb;
      //   }
    }

    .skills-link {
      color: #a761ff;

      //   &:after {
      //     content: "";
      //     position: absolute;
      //     top: inherit;
      //     left: inherit;
      //     box-shadow: -35px 14px 65px 25px #a761ff;
      //   }
    }

    .projects-link {
      color: #ff6057;

      //   &:after {
      //     content: "";
      //     position: absolute;
      //     top: inherit;
      //     left: inherit;
      //     box-shadow: -40px 14px 65px 25px #ff6057;
      //   }
    }
  }
}
